.graphic, .photography{
  position: relative;
  display: block;
  
  img{
    display: block;
  }
  .content-wrapper{
    position: absolute;
    bottom: 4rem;
    padding: 0 2rem;
  }
  h3{
    text-align: center;
    font-size: 2.3rem;
    font-family: "Fraunces";
    margin-top: 3rem;
    width: 70%;
    margin: 3.4rem auto 2rem;
    // border: dotted;
    color: rgb(62, 62, 62);


  }
  p{
    font-size: 1.5rem;
    color:rgb(62, 62, 62);
  }
}

@media screen and (min-width: 46rem ) {
.service{
  display: grid;
  grid-template-columns: 1fr 1fr;
  .graphic, .photography{
    height: auto;
    overflow: hidden;

  .content-wrapper{
    bottom: 10%;
    margin: 0 auto;
    width: 100%;
  }
  img{
    width: 100%;
    max-width: none;
    height: auto;

  }

  h3{
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
  p{
    font-size: .9rem;
    text-align: center;
    width: 19rem;
    margin: 0 auto;
  
  }
  }
}

}